/* eslint-disable multiline-ternary */
import UILoader from "@components/ui-loader"
import { Loader } from "@src/shared/CommonComponents"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { getDepthValue, parseStrAndField } from "@utils"
import "moment/locale/id"
import { useContext, useState } from "react"
import toast from "react-hot-toast"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"
import { Alert, Modal, ModalBody, ModalHeader } from "reactstrap"

import DynamicForm from "@src/utility/generateForm/dynamicForm"
import DynamicFormLoading from "@src/utility/generateForm/dynamicFormLoading"

const ModalAddEditDev = ({
  fieldArray,
  setFieldArray,
  show,
  setShow,
  storeName,
  pageAttr,
  submitHandle
}) => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const store = useSelector((state) => state.dynamicStore)
  const { colors } = useContext(ThemeColors)
  const [loadingPostPut, setLoadingPostPut] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [active, setActive] = useState("1")
  const toggleTab = (tab) => {
    if (active !== tab) {
      setActive(tab)
    }
  }
  const storeDeveloper = useSelector((state) => state.developer)

  const dynamicTempState = useSelector((state) => state.dynamic_temp_state)

  const fieldTypeValue = {
    default: {
      size: 12
    },
    pageTitle: {
      size: 8,
      fieldLabel: "Page Title"
    },
    pageModuleTitle: {
      size: 4,
      fieldLabel: "Module Name"
    },
    pageDesc: {
      size: 12,
      type: "textarea",
      fieldLabel: "Page Description"
    }
  }

  const genFieldParts = (fieldArray) => {
    let result = []
    fieldArray.map((field) => {
      let formObj = {
        fieldName: field,
        ...(fieldTypeValue?.[field] ?? fieldTypeValue["default"])
      }
      // console.log(formObj)
      result.push(formObj)
    })
    return result
  }

  const genFieldPartsValue = (fieldArray) => {
    let result = {}
    fieldArray.map((field) => {
      // result[field] = t(getDepthValue(pageAttr, field) ?? "")
      result[field] = getDepthValue(pageAttr, field)
    })
    return result
  }

  const genDynamicForm = () => {
    // console.log(pageAttr)
    let fieldObj = genFieldParts(fieldArray)
    // console.log(fieldObj)
    return fieldObj
  }

  const genDefaultFormValues = () => {
    let fieldValue = genFieldPartsValue(fieldArray)
    // console.log(pageAttr)
    return fieldValue
  }

  const successToast = () => {
    const msg =
      pageType !== "add" ? t("Successfully Updated") : t("Successfully Created")
    return toast.success(`${msg} ${t(pageAttr?.pageTitle)}`, {
      style: {
        padding: "16px",
        color: colors.success.main,
        border: `1px solid ${colors.success.main}`
      },
      iconTheme: {
        primary: colors.success.main
      }
    })
  }

  const errorToast = (res) => {
    let errorFieldsMsg = ""
    if (res?.payload?.error?.errors) {
      const errorFields = res?.payload?.error?.errors
      for (let errorx in res?.payload?.error?.errors) {
        errorFieldsMsg = `${errorx} : ${errorFieldsMsg} - ${errorFields[errorx]}\n`
        // console.log(errorx, errorFields[errorx])
      }
    }
    return toast.error(
      `${res.payload?.error?.message} \n \n ${errorFieldsMsg}`,
      {
        style: {
          padding: "16px",
          color: colors.danger.main,
          border: `1px solid ${colors.danger.main}`
        },
        iconTheme: {
          primary: colors.danger.main
        }
      }
    )
  }

  const defaultPayload = {
    storeName: storeName,
    endpointName: pageAttr?.crudOpt?.endpointName,
    baseUrl: pageAttr?.crudOpt?.baseUrl,
    callback_page: window.location.href
  }

  const updateDataToApi = (data) => {}

  const postDataToApi = (data) => {}

  const postDataToApiMulti = (data) => {}

  const handleFormSubmit = (data) => {
    // console.log(data)
    let pageAttrEdited = { ...pageAttr, ...data }
    // console.log(pageAttrEdited)
    submitHandle(pageAttrEdited)
  }

  function handleReset() {
    setFieldArray([])
    setShow(false)
  }

  return (
    <Modal
      isOpen={show}
      toggle={() => {
        handleReset()
      }}
      className={`modal-dialog-centered modal-lg`}
    >
      <UILoader
        blocking={loadingPostPut}
        overlayColor="rgba(115, 103, 240, .1)"
        loader={<Loader loadingText={loadingText} />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => {
            handleReset()
          }}
        ></ModalHeader>
        <ModalBody className="px-sm-2 pt-50 pb-5">
          <div className="text-center mb-2">
            <h2 className="saba-detail-header mb-0">Edit Page</h2>
          </div>

          <DynamicForm
            dynamicForm={genDynamicForm()}
            pageAttr
            storeName
            handleReset={handleReset}
            handleFormSubmit={handleFormSubmit}
            defaultFormValues={genDefaultFormValues()}
            editForm={true}
          />
        </ModalBody>
      </UILoader>
    </Modal>
  )
}

export default ModalAddEditDev
