export const isFileURL = (url) => {
  try {
    const parsedURL = new URL(url)

    // Jika protokol adalah 'http' atau 'https'
    if (parsedURL.protocol === "http:" || parsedURL.protocol === "https:") {
      // Jika path adalah '/', itu adalah domain
      if (parsedURL.pathname === "/") {
        return false
      }
      // Jika path berisi ekstensi file (seperti '.jpg', '.mp3', '.pdf'), itu adalah file
      if (parsedURL.pathname.includes(".")) {
        return true
      }
    }
    return false
  } catch (err) {
    // Kesalahan parsing URL, dianggap sebagai tautan ke file
    return true
  }
}

export const renderAvatarImgIcon = (item) => {
  let componentsInput = {}
  const itemHasIcon = item.dataFrom?.value
    ? checkKeyPresenceInArray(item.dataFrom?.value, "icon")
    : !item.dataFrom?.field_img && item.dataFrom?.field_icon

  const itemHasImg = item.dataFrom?.value
    ? checkKeyPresenceInArray(item.dataFrom?.value, "img")
    : item.dataFrom?.field_img

  ////IF DROPDOWN HAS IMAGE FIELD
  if (itemHasImg) {
    const { Option, SingleValue, MultiValue } = components
    const IconOption = (props) => (
      <Option {...props}>
        {props.data.img ? (
          <img
            src={props.data.img}
            style={{
              height: "26px",
              width: "26px",
              borderRadius: "10%",
              marginRight: "10px"
            }}
          />
        ) : (
          <Avatar
            initials
            className="me-1"
            size="sm"
            content={props.data.label}
          />
        )}
        {props.data.label}
      </Option>
    )

    const IconMultiValue = (props) => {
      return (
        <MultiValue {...props}>
          {props.data.img ? (
            <img
              src={props.data.img}
              style={{
                height: "26px",
                width: "26px",
                borderRadius: "10%",
                marginRight: "10px"
              }}
            />
          ) : (
            <Avatar
              initials
              className="me-1"
              size="sm"
              content={props.data.label}
            />
          )}
          {props.data.label}
        </MultiValue>
      )
    }

    const IconSingleValue = (props) => (
      <SingleValue {...props}>
        {props.data.img ? (
          <img
            src={props.data.img}
            style={{
              height: "26px",
              width: "26px",
              borderRadius: "10%",
              marginRight: "10px"
            }}
          />
        ) : (
          <Avatar
            initials
            className="me-1"
            size="sm"
            content={props.data.label}
          />
        )}
        {props.data.label}
      </SingleValue>
    )

    componentsInput = {
      components: {
        SingleValue: IconSingleValue,
        Option: IconOption
      }
    }
    if (item?.isMultiSelect) {
      componentsInput = {
        components: {
          SingleValue: IconSingleValue,
          MultiValue: IconMultiValue,
          Option: IconOption
        }
      }
    }
  } else if (!itemHasImg && itemHasIcon) {
    const { Option, SingleValue, MultiValue } = components
    const IconOption = (props) => (
      <Option {...props}>
        {renderIcon(
          props.data.icon,
          `me-1 ${props.data.class ?? props.data.className} ${
            props?.data?.color ? `text-${props?.data?.color}` : ""
          }`,
          20
        )}
        {props.data.label}
      </Option>
    )
    const IconSingleValue = (props) => (
      <SingleValue {...props}>
        {renderIcon(
          props.data.icon,
          `me-1 ${props.data.class ?? props.data.className} ${
            props?.data?.color ? `text-${props?.data?.color}` : ""
          }`,
          20
        )}
        {props.data.label}
      </SingleValue>
    )
    const IconMultiValue = (props) => {
      return (
        <MultiValue {...props}>
          <img
            src={props.data.img}
            style={{
              height: "26px",
              width: "26px",
              borderRadius: "10%",
              marginRight: "10px"
            }}
          />
          {props.data.label}
        </MultiValue>
      )
    }
    componentsInput = {
      components: {
        SingleValue: IconSingleValue,
        Option: IconOption
      }
    }
    if (item?.isMultiSelect) {
      componentsInput = {
        components: {
          SingleValue: IconSingleValue,
          MultiValue: IconMultiValue,
          Option: IconOption
        }
      }
    }
  }
  return componentsInput
}

