            // )

            // if (
            //   index !== undefined &&
            //   inputOnGrid[column?.fieldName] &&
            //   inputOnGrid[column.fieldName][index]
            // ) {
            //   inputOnGrid[column.fieldName][index]["value"] = newValue
            //   setInputOnGrid((prevState) => {
