// ** React Imports
import { useEffect, Fragment, useState, useRef } from "react"

// ** Third Party Components
import classnames from "classnames"
import PerfectScrollbar from "react-perfect-scrollbar"

// ** Vertical Menu Components
import VerticalMenuHeader from "./VerticalMenuHeader"
import VerticalNavMenuItems from "./VerticalNavMenuItems"

import { useSelector, useDispatch } from "react-redux"
import { ReactSortable } from "react-sortablejs"
import {
  updateMenu,
  updateMenuSortToApi
} from "@src/views/apps/menu/store/my_menu"
import SortableTree, {
  toggleExpandedForAll,
  getFlatDataFromTree,
  getTreeFromFlatData
} from "@nosferatu500/react-sortable-tree"

const Sidebar = (props) => {
  // ** Props
  const { menuCollapsed, menu, skin, menuData, parentItem } = props
  const dispatch = useDispatch()
  // ** States
  const [groupOpen, setGroupOpen] = useState([])
  const [groupActive, setGroupActive] = useState([])
  const [currentActiveGroup, setCurrentActiveGroup] = useState([])
  const [activeItem, setActiveItem] = useState(null)
  const storeDev = useSelector((state) => state.developer)
  const storeMenu = useSelector((state) => state.my_menu)
  // ** Menu Hover State
  const [menuHover, setMenuHover] = useState(false)

  // ** Ref
  const shadowRef = useRef(null)

  // ** Function to handle Mouse Enter
  const onMouseEnter = () => {
    setMenuHover(true)
  }

  const doOrder = (newState, sortable, store) => {
    if (
      store.dragging &&
      store.dragging.props &&
      // && JSON.stringify(menuData) !== JSON.stringify(newState)
      JSON.stringify(store.dragging.props.list) !== JSON.stringify(newState) &&
      !storeMenu.isLoadingPostPut
    ) {
      console.log("doOrder1", newState)
      // console.log(newState)
      // console.log(sortable)
      // e.preventDefault()
      dispatch(updateMenu(newState)).then((res) => {
        console.log(res)
      })
    }
  }

  // ** Scroll Menu
  const scrollMenu = (container) => {
    if (shadowRef && container.scrollTop > 0) {
      if (!shadowRef.current.classList.contains("d-block")) {
        shadowRef.current.classList.add("d-block")
      }
    } else {
      if (shadowRef.current.classList.contains("d-block")) {
        shadowRef.current.classList.remove("d-block")
      }
    }
  }

  // useEffect(() => {
  //   console.log(parentItem)
  //   console.log(groupActive)
  // }, [menuData])

  const getMenuData = () => {
    return menuData
  }

  return (
    <Fragment>
      <div
        className={classnames(
          "main-menu menu-fixed menu-accordion menu-shadow",
          {
            expanded: menuHover || menuCollapsed === false,
            "menu-light": skin !== "semi-dark" && skin !== "dark",
            "menu-dark": skin === "semi-dark" || skin === "dark"
          }
        )}
        onMouseEnter={onMouseEnter}
        onMouseLeave={() => setMenuHover(false)}
      >
        {menu ? (
          menu({ ...props })
        ) : (
          <Fragment>
            {/* Vertical Menu Header */}
            <VerticalMenuHeader
              setGroupOpen={setGroupOpen}
              menuHover={menuHover}
              {...props}
            />
            {/* Vertical Menu Header Shadow */}
            <div className="shadow-bottom" ref={shadowRef}></div>
            {/* Perfect Scrollbar */}
            <PerfectScrollbar
              className="main-menu-content"
              options={{ wheelPropagation: false }}
              onScrollY={(container) => scrollMenu(container)}
            >
              {storeDev.editMode ? (
                // <SortableTree
                //   tag="ul"
                //   className="navigation navigation-main"
                //   // treeData={menuData}
                //   treeData={getTreeFromFlatData({
                //     flatData: menuData.map((node) => ({
                //       ...node,
                //       title: node.title
                //     })),
                //     getKey: (node) => `${node.id}`,
                //     getParentKey: (node) => `${node.parent_id}`,
                //     rootKey: null
                //   })}
                //   // theme={FileExplorerTheme}
                //   // generateNodeProps={({ node, path }) => ({})}
                // />
                <ReactSortable
                  dataIdAttr="data-id"
                  animation={150}
                  // group={"nested"}
                  group={{ name: `root`, pull: true, put: true }}
                  // delayOnTouchStart={true}
                  delay={10}
                  fallbackOnBody={true}
                  swapThreshold="0.65"
                  tag="ul"
                  className={`navigation navigation-main group_menu_${menuData[0]?.parent_id}`}
                  list={menuData}
                  // list={getMenuData()}
                  filter=".disabled-drag"
                  // draggable=".nav-item"
                  setList={doOrder}
                  // swap={true}
                  // swapClass={"highlight"}
                  // nested={true}
                  onEnd={(evt) => {
                    let itemEl = evt.item
                    // console.log(evt)
                    // console.log(evt.newIndex)
                    // console.log(itemEl.getAttribute("data-id"))
                    const id = parseInt(
                      itemEl.getAttribute("data-id").replace("menu_", "")
                    )
                    dispatch(
                      updateMenuSortToApi({
                        id: id,
                        index: evt.newIndex
                      })
                    ).then((res) => {
                      console.log(res)
                    })
                  }}
                >
                  <VerticalNavMenuItems
                    items={menuData}
                    menuData={menuData}
                    menuHover={menuHover}
                    groupOpen={groupOpen}
                    activeItem={activeItem}
                    groupActive={groupActive}
                    setGroupOpen={setGroupOpen}
                    menuCollapsed={menuCollapsed}
                    setActiveItem={setActiveItem}
                    setGroupActive={setGroupActive}
                    currentActiveGroup={currentActiveGroup}
                    setCurrentActiveGroup={setCurrentActiveGroup}
                    devMode={storeDev.editMode}
                  />
                </ReactSortable>
              ) : (
                <ul className="navigation navigation-main">
                  <VerticalNavMenuItems
                    items={menuData}
                    menuData={menuData}
                    menuHover={menuHover}
                    groupOpen={groupOpen}
                    activeItem={activeItem}
                    groupActive={groupActive}
                    setGroupOpen={setGroupOpen}
                    menuCollapsed={menuCollapsed}
                    setActiveItem={setActiveItem}
                    setGroupActive={setGroupActive}
                    currentActiveGroup={currentActiveGroup}
                    setCurrentActiveGroup={setCurrentActiveGroup}
                  />
                </ul>
              )}
            </PerfectScrollbar>
          </Fragment>
        )}
      </div>
    </Fragment>
  )
}

export default Sidebar
